<template>
    <div class="m-pet-tabs">
        <div class="u-tabs">
            <!-- <div class="u-tabs-btn">
                <i class="el-icon-arrow-left"></i>
            </div>
            <div class="u-tabs-btn right">
                <i class="el-icon-arrow-right"></i>
            </div> -->
            <div v-for="item in types" :key="item.type">
                <div
                    class="u-tab-item"
                    @click="clickTabs(item)"
                    :class="{
                        active: item.class == active,
                    }"
                >
                    <!-- <img class="u-icon-img" :src="getThumbnail(item.label)" :alt="item.label" /> -->
                    <span class="u-tabs-span">{{ item.name }}</span>
                </div>
            </div>

            <div class="u-tabs-filter">
                <el-popover
                    placement="bottom-end"
                    trigger="click"
                    width="90"
                    v-model="filterOpen"
                    popper-class="u-filter-popover"
                >
                    <div class="u-tabs-marks">
                        <el-radio-group v-model="petSource">
                            <el-radio-button
                                class="u-source"
                                v-for="(item, index) in Source"
                                :key="'laiyuan' + index"
                                :label="item.source"
                                size="mini"
                                >{{ item.name }}</el-radio-button
                            >
                        </el-radio-group>
                    </div>
                    <svg width="40" height="40" viewBox="0 0 40 40" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:slot="'reference'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><rect width="40" height="40" rx="20" fill="transparent"/><path d="M12.25 13.61C14.27 16.2 18 21 18 21v6c0 .55.45 1 1 1h2c.55 0 1-.45 1-1v-6s3.72-4.8 5.74-7.39a.998.998 0 00-.79-1.61H13.04c-.83 0-1.3.95-.79 1.61z" fill="tranparent"/></svg>
                </el-popover>
            </div>
            <div class="u-tabs-search">
                <el-input placeholder="请输入搜索内容" v-model="title" suffix-icon="el-icon-search" class="u-search" />
            </div>
        </div>
    </div>
</template>

<script>
import { __imgPath } from "@jx3box/jx3box-common/data/jx3box";
export default {
    name: "tabs",
    props: ["types", "Source", "active"],
    data: function () {
        return {
            petSource: "",
            title: "",
            filterOpen: false,
        };
    },
    computed: {
        params() {
            const _params = {};
            if (this.active) {
                _params.Class = this.active;
            }
            if (this.petSource) {
                _params.Source = this.petSource;
            }
            if (this.title) {
                _params.Name = this.title;
            }
            return _params;
        },
    },
    methods: {
        //切换数据
        clickTabs(item) {
            // this.active = value;
            this.$emit("setActive", item.class);
        },
    },
    watch: {
        params: {
            deep: true,
            immediate: true,
            handler: function (obj) {
                this.$emit("change", obj);
            },
        },
    },
};
</script>
